import React, { useState, useEffect, useMemo } from "react";
import { useParams, useLocation } from "react-router-dom";
import { getProjectById } from "../../services";
import { toast } from "react-toastify";
import Section from "../Section/Section";
import { BackLink } from "./ProjectDetails.styled";
import { IoMdArrowRoundBack } from "react-icons/io";
import { Table, Row } from "./ProjectDetails.styled";

const ProjectDetails = () => {
  const { id } = useParams();

  const [project, setProject] = useState(null);
  const [isLoading, setisLoading] = useState(false);
  const location = useLocation();

  useEffect(() => {
    const controller = new AbortController();
    async function getProject(id) {
      try {
        setisLoading(true);
        const response = await getProjectById(id, controller.signal);
        // console.log("response effect: ", response.data);
        setProject(response.data);
      } catch (error) {
        toast.error("Не вдалось завантажити проект");
      } finally {
        setisLoading(false);
      }
    }

    getProject(id);

    return () => {
      setProject(null);
      controller.abort();
    };
  }, [id]);

  // const totalMaterialPrice = useMemo(() => {
  //   return project?.Materials.reduce((total, material) => {
  //     return total + material.PriceInProject;
  //   }, 0);
  // }, [project]);

  // const totalServicePrice = useMemo(() => {
  //   return project?.Services.reduce((total, service) => {
  //     return total + service.PriceInProject;
  //   }, 0);
  // }, [project]);

  //   if (project.Services.Materials.length > 0) {
  //     const totalServiceMaterialPrice =
  //       project?.Services?.Materials.reduce((total, material) => {
  //         return total + material.PriceInProject;
  //       }, 0);
  //     return totalServiceMaterialPrice;
  //   }
  // };

  return (
    <Section>
      {isLoading && <p>Loading project details...</p>}
      {project && (
        <>
          <BackLink to={location.state.from}>
            <IoMdArrowRoundBack />
            Back
          </BackLink>

          <h2>{project.Title}</h2>

          <div>
            <Table>
              <caption>матеріали і послуги даного проекту</caption>
              <thead>
                <tr>
                  <th>Код</th>
                  <th>Опис</th>
                  <th>Ціна ринкова</th>
                  <th>Ціна в даному проекті</th>
                  <th>Одиниці</th>
                  <th>Витрата</th>
                  <th>Витрата в даному проекті</th>
                </tr>
              </thead>
              <tbody>
                {project.Services &&
                  project.Services.map((service) => {
                    return (
                      <Row key={service._id}>
                        <td>{service.Code}</td>
                        <td>{service.DescriptionUA}</td>
                        <td>{service.PriceUAH}</td>
                        <td>{service.PriceInProject}</td>
                        <td>{service.Unit}</td>
                        <td>{service.Consumption}</td>
                        <td>{service.ConsumptionInProject}</td>
                        {service.Materials?.length > 0 &&
                          service.Materials.map((material) => {
                            return (
                              <Row key={material._id}>
                                <td>{material.Code}</td>
                                <td>{material.DescriptionUA}</td>
                                <td>{material.PriceUAH}</td>
                                <td>{material.PriceInProject}</td>
                                <td>{material.Unit}</td>
                                <td>{material.Consumption}</td>
                                <td>{material.ConsumptionInProject}</td>
                              </Row>
                            );
                          })}
                      </Row>
                    );
                  })}

                {project.Materials &&
                  project.Materials.map((material) => {
                    return (
                      <Row key={material._id}>
                        <td>{material.Code}</td>
                        <td>{material.DescriptionUA}</td>
                        <td>{material.PriceUAH}</td>
                        <td>{material.PriceInProject}</td>
                        <td>{material.Unit}</td>
                        <td>{material.Consumption}</td>
                        <td>{material.ConsumptionInProject}</td>
                      </Row>
                    );
                  })}
              </tbody>
              <tfoot>
                <td>Сума:</td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
                <td></td>
              </tfoot>
            </Table>
          </div>

          {/* {project.Materials.length > 0 ? (
            <>
              <div
                style={{
                  marginLeft: 16,
                }}
              >
                <ProjectMaterial material={tableHeader} />
              </div>
              <List>
                {project.Materials.map((material) => (
                  <Item key={material._id}>
                    <ProjectMaterial material={material} />
                  </Item>
                ))}
              </List>
              {project?.Materials.length > 0 && (
                <div>
                  <p>
                    Кількість матеріалів:
                    <span>{project.Materials.length}</span>
                  </p>
                  <p>
                    Загальна вартість: <span>{totalPrice} &#8372;</span>
                  </p>
                </div>
              )}
            </>
          ) : (
            <p>У вашому проекті немає матеріалів</p>
          )} */}
        </>
      )}
    </Section>
  );
};

export default ProjectDetails;
